function prepareMatrix(cellNames, mat) {
    let j;
    let i = 0;
    const matDict = {};
    mat.forEach(row => {
        j = 0;
        const rowDict = {};
        row.forEach(elm => rowDict[cellNames[j++]] = elm);
        matDict[cellNames[i++]] = rowDict;
    });
    return matDict;
}
